import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';

import Box from './Box';
import Button from './Button';

const AbsButton = styled(Button)`
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
`;

const CircleButton = ({
  children,
  border,
  borderColor,
  bg,
  is,
  hoverColor,
  hoverBg,
  hoverBorder,
  onClick,
  color,
  ...props
}) => (
  <Box {...props}>
    <Box position="relative" pt="100%">
      <AbsButton
        is={is}
        px="0"
        py="0"
        w={1}
        color={color}
        borderRadius="50%"
        border={border}
        borderColor={borderColor}
        bg={bg}
        hoverColor={hoverColor}
        hoverBg={hoverBg}
        hoverBorder={hoverBorder}
        onClick={onClick}
      >
        <Box
          top="50%"
          transform="translateY(-50%)"
          position="absolute"
          w={1}
          textAlign="center"
        >
          {children}
        </Box>
      </AbsButton>
    </Box>
  </Box>
);

CircleButton.propTypes = {
  children: PropTypes.node,
  border: PropTypes.string,
  borderColor: PropTypes.string,
  bg: PropTypes.string,
  is: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.func,
  ]),
};

export default CircleButton;



